<template>
  <div class="">
    <b-form @submit.prevent="onSubmit" ref="form">
      <b-form-group v-slot="{ ariaDescribedby }" class="mb-2">
        <b-row class="mb-2">
          <b-col lg="3" md="6" class="pl-0 ml-3">
            <b-form-radio v-model="form.type" :aria-describedby="ariaDescribedby" name="some-radios" value="Feedback"
              >Feedback <b-img src="/img/feedbackup/feedback.png" class="mb-2 feedbackup-image"
            /></b-form-radio>
          </b-col>
          <b-col lg="3" md="6" class="pl-0 ml-3">
            <b-form-radio v-model="form.type" :aria-describedby="ariaDescribedby" name="some-radios" value="Bug">Bug <b-img src="/img/feedbackup/bug.png" class="ml-1 mb-1 bug-image"/></b-form-radio>
          </b-col>
        </b-row>
      </b-form-group>
      <b-form-group label="Title" class="mb-2">
        <b-form-input v-model="form.title" type="text" placeholder="Enter Title" maxlength="100" required></b-form-input>
        <span class="mb-0 text-muted small-2 float-right">{{ form.title.length }}/100</span>
      </b-form-group>
      <b-form-group label="Description" class="mb-4">
        <QuillEditor ref="quill_editor_ref" :value="form.description" class="mb-5" />
      </b-form-group>
      <p v-if="err_msg" class="float-right mr-4 text-danger">Please add description</p>
      <b-form-group label="Name" v-if="visitor_id == 0 || visitor_name == ''" class="mb-2">
        <b-form-input v-model="visitor_form.name" type="text" placeholder="Enter Your Name" autocomplete="on" maxlength="50" required></b-form-input>
        <span class="mb-0 text-muted small-2 float-right">{{ visitor_form.name.length }}/50</span>
      </b-form-group>
      <b-form-group label="Email" v-if="visitor_id == 0 || visitor_name == ''" class="">
        <b-form-input v-model="visitor_form.email" type="email" placeholder="Enter Email" autocomplete="on" required></b-form-input>
      </b-form-group>
      <b-form-checkbox id="checkbox" v-model="form.notify" name="checkbox">
        Notify any changes or responses.
      </b-form-checkbox>
      <div class="captcha mt-4 pt-3 mb-2 w-100">
        <Captcha :key="render" />
      </div>
      <div class="d-flex justify-content-end border-top mt-4 pt-4">
        <p class="text-success float-right mt-2 mr-2" v-if="suc_msg">Feedback added successfully!!</p>
        <b-button type="submit" variant="primary" class="px-5" v-if="captcha_msg">{{ btn_save }}</b-button>
        <b-button type="submit" variant="primary" class="px-5" v-if="!captcha_msg" disabled>{{ btn_save }}</b-button>
      </div>
    </b-form>
  </div>
</template>
<script>
  import Captcha from "../form/components/input/Recaptcha.vue";
  import { bus } from "@/js/core/emit-bus";
  import FeedbackUp from "@/js/feedbackup/feedbackup.js";
  import QuillEditor from "./QuillEditor.vue";

  export default {
    components: { Captcha, QuillEditor },
    props: {
      visitor_id: String,
      visitor_name: String,
    },
    data() {
      return {
        err_msg: false,
        suc_msg: false,
        types: [
          { value: "Feedback", text: "Feedback" },
          { value: "Bug", text: "Bug" },
        ],
        btn_save: "Save",
        render: 0,
        captcha_msg: false,
        visitor_form: {
          name: "",
          email: "",
        },
        form: {
          title: "",
          description: "",
          notify: true,
          type: "Feedback",
        },
      };
    },
    mounted() {
      this.render++;
      this.captcha_msg = false;
      this.$root.$on("recaptcha_status", (response) => {
        console.log("response", response);
        this.captcha_msg = response.success;
      });
    },
    destroyed() {
      this.$root.$off("recaptcha_status");
    },
    methods: {
      async onSubmit() {
        this.err_msg = false;
        if (this.$refs.quill_editor_ref.description.replace(/( |<([^>]+)>)/gi, "").trim() == "" && !this.$refs.quill_editor_ref.description.includes("<img")) {
          this.err_msg = true;
          setTimeout(() => {
            this.err_msg = false;
          }, 1500);
          return;
        }
        this.captcha_msg = false;
        this.btn_save = "Saving";
        this.form.visitor_id = this.visitor_id;
        if (this.visitor_id == 0) {
          var response = await FeedbackUp.addVisitor(this.visitor_form);
          this.$parent.$parent.visitor_id = response[0].GENERATED_KEY;
          this.$parent.$parent.visitor_name = this.visitor_form.name;
          this.form.visitor_id = response[0].GENERATED_KEY;
        } else if (this.visitor_id != 0 && this.visitor_name == "") {
          await FeedbackUp.updateVisitor(this.visitor_form, this.visitor_id);
          this.$parent.$parent.visitor_name = this.visitor_form.name;
        }
        this.form.project_id = window.project_id;
        this.form.domain_id = window.domain;
        this.form.created_by = window.user;
        this.form.notify = this.form.notify ? 1 : 0;
        var data = [];
        this.form.description = this.$refs.quill_editor_ref.description;
        if (this.form.id) {
          data = await FeedbackUp.updateFeedback(this.form);
        } else {
          data = await FeedbackUp.addFeedback(this.form);
        }
        this.captcha_msg = true;
        this.btn_save = "Saved";
        this.suc_msg = true;
        setTimeout(() => {
          this.suc_msg = false;
          bus.$emit("submitFeedback", data, this.form.id);
        }, 1500);
      },
    },
  };
</script>
<style>
  .quill-editor {
    position: relative;
    height: 140px !important;
  }
  .addfeedback-custom-control .custom-control {
    padding: 0.5rem 1.2rem;
  }
  .feedbackup-image {
    height: 21px;
  }
  .bug-image {
    height: 17px;
  }
</style>
